import React, { useState } from 'react'
import GoBack from '../components/GoBack/GoBack';
import cl from '../editingPages.module.scss';
import * as Yup from 'yup';
import { Formik, Form, Field } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import { getHeaders } from '../../../API/APISettings';
import { useNavigate } from 'react-router-dom';
// import { store } from '../../../reducers/index';
// import { CHANGE_USER_INFO } from '../../../reducers/auth';
// import { useSelector } from 'react-redux';


import { setLocale } from 'yup';


import Loader from '../../../components/UI/Loader/Loader';
import MainLayout from '../components/MainLayout/MainLayout';
import { SERVER_URL } from '../../../__CONSTANTS';



function EditPhotoPage() {
    const navigate = useNavigate()


    const [photoFieldValue, setPhotoFieldValue] = useState(null)

    const [errorsArr, setErrorsArr] = useState([])

    const [changePhoto, LoadingChangePhoto] = useFetching(async (values) => {

        const body = { photo: photoFieldValue.name }

        console.log(photoFieldValue.name)

        // return

        // let data_is_the_same = body.username === initial_username

        // if (data_is_the_same) {
        //     navigate(-1)
        //     return
        // }


        await axios.post(`${SERVER_URL}/users/edit_photo/`, body, {
            headers: {
                ...getHeaders(),
                // 'content-type': 'multipart/form-data'
            },
        })
            .then(res => {
                setErrorsArr([])

                // store.dispatch({
                //     type: CHANGE_USER_INFO,
                //     payload: res.data.user,
                // })

                // navigate(-1)
            })
            .catch(err => {
                switch (err.response?.status) {
                    case "400":
                        setErrorsArr(err.response.data.messages)
                        break;
                    case "401":
                        setErrorsArr(["Unauthorized"])
                        break;
                    case "500":
                        setErrorsArr(["Internal Server Error"])
                        break;
                    default:
                        setErrorsArr(["Smth went wrong. Try again"])
                }
            });
    })


    function onSubmit(e) {
        e.preventDefault()

        changePhoto()
    }

    return (

        <MainLayout>
            {LoadingChangePhoto &&
                <div className='loader_wrapper'>
                    <Loader />
                </div>
            }
            <form onSubmit={onSubmit}>
                <input
                    type="file"
                    onChange={(e) => {
                        setPhotoFieldValue(e.target.files[0])
                        // URL.createObjectURL(  // )                   
                    }}
                />
                <button
                    type='sumbit'
                >
                    Submit
                </button>
            </form>
        </MainLayout>
    )
}

export default EditPhotoPage