import React from 'react';

import cl from '../Navbar.module.scss'
import { NavLink } from 'react-router-dom';


const Nav = function (props) {

    const { t } = props

    const navlinks = [
        { 'name': `${t('main_content.menu.main', "main")}`, 'link': '/' },
        { 'name': `${t('main_content.menu.about_me', "about me")}`, 'link': 'about-me' },
        { 'name': `${t('main_content.menu.contact_me', "contact me")}`, 'link': 'contact-me' }
    ]

    return (
        <nav className={cl.nav}>
            {/* // nav_links_wrapper */}

            {navlinks.map((item, index) =>
                <NavLink
                    key={index}

                    to={item.link}

                    data-title={`  ${item.name}`}
                    className={
                        (navData) => navData.isActive ? `${cl.nav__link} ${cl.nav__link_active} nav__link nav__link_active` : `${cl.nav__link} nav__link`
                    }
                >
                    {item.name}
                </NavLink>
            )}

        </nav>
    )
}

export default Nav;