import React, { useEffect, useState, useRef } from 'react'

import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';

import { w3cwebsocket as W3CWebSocket } from "websocket";
import debounce from "lodash/debounce";
import Cookies from 'js-cookie'

import ChatTopHeader from '../_components/TopHeader/TopHeader';
import MessagesWrapper from '../_components/MessagesWrapper/MessagesWrapper';
import MessageForm from '../_components/MessageForm/MessageForm';

import cl from '../chatPages.module.scss';
import { CHAT_WEBSOCKET_RECONNECTING_TIMEOUT, DELAY_OF_HANDLING_MEMBER_TYPING, SERVER_ADDRESS } from '../../../__CONSTANTS';


// TODO
// ___ TYPESCRIPT
// - messages types
// - actions


function ChatRoomPage() {
    const params = useParams()

    const [message, setMessage] = useState('')

    const [messages, setMessages] = useState({})

    const [outgoingMessagesOnLoad, setOutgoingMessagesOnLoad] = useState([])

    const [isUserTyping, setIsUserTyping] = useState(false)

    const [memberThatIsTyping, setMemberThatIsTyping] = useState(null)

    const handleIsMemberTyping = debounce(function () {
        // continually delays setting "memberThatIsTyping" to null for {number}ms until the member has stopped typing and the delay runs out
        setMemberThatIsTyping(null);
    }, DELAY_OF_HANDLING_MEMBER_TYPING);

    const scrollDown = () => {
        if (messages_wrapper_ref.current) {
            messages_wrapper_ref.current.scrollTop = messages_wrapper_ref.current.scrollHeight
        }
    }

    const messages_wrapper_ref = useRef()

    const chatSocketRef = useRef(null)
    const [waitingToReconnect, setWaitingToReconnect] = useState(null)

    const [connected, setConnected] = useState(false) // null

    const ROOMNAME = params.room_name

    const USERNAME = useSelector(state => state.auth?.user?.username) || Cookies.get("username")


    useEffect(() => {

        if (isUserTyping) {

            if (chatSocketRef.current) {

                try {
                    chatSocketRef.current.send(JSON.stringify({
                        action: 'user_is_typing',
                        data: {},
                    }))
                } catch (e) {
                    console.log(e)
                }

            }

        }

    }, [isUserTyping])


    const sendMessage = (message) => {

        if (connected) {

            if (chatSocketRef.current) {

                chatSocketRef.current.send(JSON.stringify({
                    action: 'send_message',
                    data: {
                        message: message,
                    }
                }))

            }

        }
    }

    const onSubmit = () => {

        let m = message.trim()

        sendMessage(m)

        const new_message = {
            "type": "text",
            "username": USERNAME,
            "message": m,
            "isLoading": true,
        }

        new Promise((resolve) => {

            setOutgoingMessagesOnLoad(
                [
                    ...outgoingMessagesOnLoad,
                    new_message
                ]
            )

            resolve(1)
        }).then(() => {
            scrollDown()
        })

        setMessage('')
    }


    useEffect(() => {

        if (chatSocketRef.current) {
            chatSocketRef.current.onmessage = (event) => {

                const data = JSON.parse(event["data"])

                switch (data["action"]) {
                    case "user_is_typing":
                        if (data["username"] !== USERNAME) {
                            setMemberThatIsTyping(data["username"])
                            handleIsMemberTyping()
                        }
                        break;
                    case "send_message": // Receiving the messages

                        const new_message = data["message"]

                        new Promise((resolve) => {


                            setOutgoingMessagesOnLoad([])

                            setMessages({
                                ...messages,
                                "Today": [...messages["Today"] || [], new_message]
                            })

                            resolve(1)
                        }).then(() => {
                            if (data["message"]["username"] === USERNAME) {
                                scrollDown()
                            }
                            scrollDown()
                        })


                        break;
                    case "send_messages": // Receiving array of the messages
                        const new_messages = data["messages"]

                        setOutgoingMessagesOnLoad([])

                        setMessages({
                            ...messages,
                            "Today": [...messages["Today"] || [], ...new_messages]
                        })

                        break;
                    default:
                        console.log("OnMessage: Unknown action")
                        break;
                }

            }
        }

    }, [messages, waitingToReconnect, chatSocketRef.current]) // ?



    useEffect(() => {
        if (waitingToReconnect) { return }

        if (!chatSocketRef.current) {
            chatSocketRef.current = new W3CWebSocket(
                `ws://${SERVER_ADDRESS}/ws/chat/${ROOMNAME}/`
            )

            const chatSocket = chatSocketRef.current


            chatSocket.onopen = function () {
                console.log("opened")

                new Promise((resolve) => {

                    setConnected(true)

                    resolve(connected)

                })
                    .then(() => {

                        if (outgoingMessagesOnLoad.length) {

                            chatSocket.send(JSON.stringify({
                                action: 'send_messages',
                                data: {
                                    messages: outgoingMessagesOnLoad.map(m => m["message"])
                                }
                            }))

                        }

                    })

            }

            chatSocket.onerror = (e) => { console.log(e) }

            chatSocket.onclose = () => {
                console.log("closed connection")

                if (chatSocketRef.current) {
                    console.log("Closed by server")
                } else {
                    console.log("Closed by app component unmount")
                    return
                }

                if (waitingToReconnect) {
                    return
                }

                setConnected(false)

                setWaitingToReconnect(true)

                setTimeout(function () {

                    console.log("Waiting to reconnect")

                    setWaitingToReconnect(null)
                }, CHAT_WEBSOCKET_RECONNECTING_TIMEOUT);

            }

        }

        return () => {

            chatSocketRef.current = null
        }

    }, [waitingToReconnect])


    useEffect(() => {
        document.getElementsByTagName("body")[0].style.overflow = 'hidden'
        scrollDown() // ?

        return () => {
            document.getElementsByTagName("body")[0].style.overflow = "auto";
        }
    }, [])


    return (
        <div className={cl.main_wrapper}>
            <div className={cl.main_content_wrapper}>
                <div className={cl.main_content}>
                    <div className={cl.main}>

                        <ChatTopHeader
                            roomname={ROOMNAME}
                            connected={connected}
                            memberThatIsTyping={memberThatIsTyping}
                        />

                        <MessagesWrapper
                            messages={messages}
                            outgoingMessagesOnLoad={outgoingMessagesOnLoad}
                            username={USERNAME}
                            scrollDown={scrollDown}
                            messages_wrapper_ref={messages_wrapper_ref}
                        />

                        <div className={cl.messageFormWrapper}>
                            <MessageForm
                                message={message}
                                setMessage={setMessage}
                                onSubmit={onSubmit}
                                setIsUserTyping={setIsUserTyping}
                            />
                        </div>

                    </div>

                </div>

            </div>
        </div>
    )
}

export default ChatRoomPage;