import React, { Suspense } from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import i18next from 'i18next';
import { initReactI18next } from 'react-i18next';
import HttpApi from 'i18next-http-backend';
import LanguageDetector from 'i18next-browser-languagedetector';

import { languagesList } from './languages/languages';
import Loader from './components/UI/Loader/Loader';
import { store } from './reducers/index';


import { transitions, positions, Provider as AlertProvider } from 'react-alert'
import AlertTemplate from 'react-alert-template-basic';


i18next
  .use(initReactI18next)
  .use(HttpApi)
  .use(LanguageDetector)
  .init({
    supportedLngs: languagesList,
    fallbackLng: 'en',
    debug: false,
    detection: {
      order: ['path', 'cookie', 'htmlTag'],
      caches: ['cookie'],
    },
    backend: {
      loadPath: `${window.location.origin}/alanominator/assets/locales/{{lng}}/translation.json`,
    },
  })


const L = () =>
  <div className='loader_wrapper'>
    <Loader />
  </div>


const UserLoader = function () {
  const user_loading = store.getState().auth.isLoading

  return (
    <>
      {user_loading ?
        <div className='loader_wrapper'>
          <Loader />
        </div> : <></>
      }
    </>
  )
}

const alert_options = {
  position: positions.BOTTOM_CENTER,
  timeout: 2000,
  offset: '30px',
  transition: transitions.SCALE,
  containerStyle: {
    zIndex: 10000,
  }
}


const root = ReactDOM.createRoot(document.getElementById('root')!)

root.render(
  <Suspense fallback={<L />}>
    <AlertProvider template={AlertTemplate} {...alert_options}>
      <UserLoader />
      <App />
    </AlertProvider>
  </Suspense>
)