import React, { useState } from 'react'
import * as Yup from 'yup';
import { Formik, Form } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import Button from '../_components/FormButton/FormButton';
import { Link, useNavigate } from 'react-router-dom';
import { store } from '../../../reducers/index';
import { LOGIN_SUCCESS, LOGIN_FAIL } from '../../../reducers/auth';
import cl from '../authPages.module.scss';
import Loader from '../../../components/UI/Loader/Loader';
import { useTranslation } from 'react-i18next';

import { useAlert } from 'react-alert';
import { SERVER_URL } from '../../../__CONSTANTS';
import { is_object_empty } from '../../../utils/is_object_empty';
import Input from '../_components/FormInput/FormInput';



function LoginPage() {

  const alert = useAlert()

  const { t } = useTranslation()

  const navigate = useNavigate()

  const initialValues = {
    email: '',
    password: ''
  }


  let required = t('main_content.pages.auth_pages.required', "Required")

  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .email(t('main_content.pages.auth_pages.ief', "Invalid email format"))
      .required(required),
    password: Yup.string()
      .required(required)
  })

  const [responseError, setResponseError] = useState<string>("")

  const [loginUser, LoadingLoginUser] = useFetching(async (values: object) => {
    const body = JSON.stringify(values);

    await axios.post(`${SERVER_URL}/users/auth/login/`, body, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(res => {
        alert.success(res.data.message)

        new Promise(function (resolve, reject) {
          store.dispatch({
            type: LOGIN_SUCCESS,
            payload: res.data,
          })

          return resolve(1)

        })
          .then(() => {
            navigate("/")
          })
      })
      .catch(err => {
        err.response ?
          setResponseError(err.response.data.message)
          : setResponseError('Smth went wrong. Try again.')

        store.dispatch({
          type: LOGIN_FAIL,
          payload: "Network error"
        });
      });
  })

  function onSubmit(values) {
    loginUser(values)
  }

  return (
    <>
      {LoadingLoginUser &&
        <div className='loader_wrapper'>
          <Loader />
        </div>
      }
      {responseError &&
        <div className={`${cl.errorMessageWrapper}`}>
          <span className={`${cl.errorMessage} errorMessage`}>
            {responseError}
          </span>
        </div>
      }

      <div className={cl.form_wrapper}>
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onSubmit}
        >
          {({ errors, touched, values }) => (
            <Form
              className={`${cl.form} form`}
            >
              <b className={`${cl.page_name} auth_page_name`}>
                {t('main_content.pages.auth_pages.login', "login")}
              </b>

              <Input
                name="email"
                placeholder={t('main_content.pages.auth_pages.email', "email")}
                type="email"
                autoFocus={true}
                value={values["email"]}
                touched={touched["email"]}
                hasError={errors["email"]}
                spellCheck="false"
              // inputmode="email"
              />
              <Input
                name="password"
                placeholder={t('main_content.pages.auth_pages.password', "password")}
                type="password"
                value={values["password"]}
                touched={touched["password"]}
                hasError={errors["password"]}
              />

              <Button
                disabled={
                  LoadingLoginUser ||
                  !(is_object_empty(errors) && !is_object_empty(touched))
                }
                type="submit"
              >
                {t('main_content.pages.auth_pages.sign_in', "sign in")}
              </Button>

              <div className={cl.lw}>
                <span
                  className={cl.ttl}
                >
                  {t('main_content.pages.auth_pages.dha', "Don't have an account?")}
                </span>
                <br />
                <Link
                  className={`${cl.link} link_to_another_auth_page`}
                  to="/users/register"
                >
                  {t('main_content.pages.auth_pages.register', "register")}
                </Link>

                <hr />

                <Link className={`${cl.ftp} ftp`} to="/users/reset_password">
                  {t('main_content.pages.auth_pages.forgot_the_password', "Forgot the password?")}
                </Link>


              </div>



            </Form>
          )}
        </Formik>
      </div>
    </>
  )
}

export default LoginPage;