export function format_message_string(str: string) {
    // `Formats string with special characters to html string`

    str = replaceHtmlTagsToHtmlEntities(str)

    str = format_links(str)

    str = turn_characters_into_html_tags(str)

    return str
}


const format_links = (str: string) => {
    // TODO regex domain names

    const urlRegex = /(https?:\/\/(?:www\.|(?!www))[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|www\.[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|https?:\/\/(?:www\.|(?!www))[a-zA-Z0-9]+\.[^\s]{2,}|www\.[a-zA-Z0-9]+\.[^\s]{2,})/gi

    str = str.replaceAll(urlRegex, function ($0) {

        let link = $0

        return `<a href="${link}">${link}</a>`
    })

    return str
}

const replaceHtmlTagsToHtmlEntities = (str: string) => {
    const __html_entities_dict = {
        '&': '&amp;',
        ';': '&#59',
        // '/': '&#47;',
        '<': '&lt;',
        '>': '&gt;',
        "'": '&apos;',
        '"': '&quot;',
    }

    Object.keys(__html_entities_dict).map(c => {
        str = str.replaceAll(c, __html_entities_dict[c])
        return true
    })

    return str
}


const turn_characters_into_html_tags = (str: string) => {

    const characters_and_tags_dict = {
        '**': "b",
        "##": "i",
        "$$": "del",
        "%%": "code",
        "||": "dfn",
        "^^": "mark",
        "__": "em",
    }

    Object.keys(characters_and_tags_dict).map((c) => {
        const regex = new RegExp(`\\${c.split('').join('\\')}`, "g")

        let count_c = (str.match(regex) || []).length

        if (count_c % 2 !== 0) { count_c -= 1 }

        for (let a = 0; a < count_c; a += 2) {

            let tag_name = characters_and_tags_dict[c]

            str = str.replace(c, `<${tag_name}>`)
            str = str.replace(c, `</${tag_name}>`)
        }

        return true
    })

    return str
}