import React, { useState } from 'react'
import GoBack from '../components/GoBack/GoBack';
import cl from '../editingPages.module.scss';
import * as Yup from 'yup';
import { Formik, Form } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import { getHeaders } from '../../../API/APISettings';
import FormInput from '../../auth_pages/_components/FormInput/FormInput';
import { useNavigate } from 'react-router-dom';
import { store } from '../../../reducers/index';
import { CHANGE_USER_INFO } from '../../../reducers/auth';
import { useSelector } from 'react-redux';


import Loader from '../../../components/UI/Loader/Loader';
import MainLayout from '../components/MainLayout/MainLayout';
import { SERVER_URL } from '../../../__CONSTANTS';



function EditUsernamePage() {
    const navigate = useNavigate()

    const initial_username = useSelector(state => state.auth.user.username)

    const initialValues = {
        username: initial_username,
    }

    const validationSchema = Yup.object().shape({
        username: Yup.string()
            .required("Required")
            .max(150),
    })

    const [errorsArr, setErrorsArr] = useState([])

    const [editUsername, LoadingEditUsername] = useFetching(async (values) => {
        const body = values

        let data_is_the_same = body.username === initial_username

        if (data_is_the_same) {
            navigate(-1)
            return
        }

        await axios.post(`${SERVER_URL}/users/edit_username/`, body, {
            headers: getHeaders(),
        })
            .then(res => {
                setErrorsArr([])

                store.dispatch({
                    type: CHANGE_USER_INFO,
                    payload: res.data.user,
                })

                navigate(-1)
            })
            .catch(err => {
                switch (err.response?.status) {
                    case "400":
                        setErrorsArr(err.response.data.messages)
                        break;
                    case "401":
                        setErrorsArr(["Unauthorized"])
                        break;
                    case "500":
                        setErrorsArr(["Internal Server Error"])
                        break;
                    default:
                        setErrorsArr(["Smth went wrong. Try again"])
                }
            });
    })


    function onSubmit(values) {
        editUsername(values)
    }

    return (

        <MainLayout>
            {LoadingEditUsername &&
                <div className='loader_wrapper'>
                    <Loader />
                </div>
            }
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
            >
                {({ errors, touched, values }) => (
                    <Form className={cl.form_wrapper}>

                        <div className={cl.top_wrapper}>
                            <GoBack />

                            <span>
                                Edit username
                            </span>

                            <button className={cl.btn} type='submit'>
                                Hello
                            </button>
                        </div>

                        <FormInput
                            name="username"
                            placeholder="username"
                            autoFocus={true}
                        />

                        {errorsArr &&
                            <div className={cl.errorMessageWrapper}>
                                {errorsArr.map((error, index) =>
                                    <span key={index} className={cl.errorMessage}>
                                        {error}
                                    </span>
                                )}
                            </div>
                        }

                    </Form>
                )}
            </Formik>
        </MainLayout>
    )
}

export default EditUsernamePage;