import React, { useState } from 'react'
import GoBack from '../components/GoBack/GoBack';
import cl from '../editingPages.module.scss';
import * as Yup from 'yup';
import { Formik, Form } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import { getHeaders } from '../../../API/APISettings';
import FormInput from '../../auth_pages/_components/FormInput/FormInput';
import { useNavigate } from 'react-router-dom';

import Loader from '../../../components/UI/Loader/Loader';
import MainLayout from '../components/MainLayout/MainLayout';
import { SERVER_URL } from '../../../__CONSTANTS';





function EditPasswordPage() {

    const navigate = useNavigate()

    const initialValues = {
        old_password: '',
        new_password: '',
        re_new_password: ''
    }

    const validationSchema = Yup.object().shape({
        old_password: Yup.string()
            .required("Required")
            .max(150),
        new_password: Yup.string()
            .required("Required")
            .min(8)
            .max(24)
            // TODO
            .oneOf([Yup.ref('re_new_password'), null], 'Passwords must match'),
        re_new_password: Yup.string()
            .required("Required")
            .min(8)
            .max(24)
            // TODO
            .oneOf([Yup.ref('new_password'), null], 'Passwords must match')
    })

    const [error, setError] = useState('')

    const [editPassword, LoadingEditPassword] = useFetching(async (values) => {
        const body = values

        await axios.post(`${SERVER_URL}/users/change_password/`, body, {
            headers: getHeaders(),
        })
            .then(res => {
                setError('')

                alert(res.data.message)

                navigate(-1)
            })
            .catch(err => {
                switch (err.response?.status) {
                    case 400:
                        setError(err.response.data.message)
                        break;
                    case 401:
                        setError("Unauthorized")
                        break;
                    case 500:
                        setError("Internal Server Error")
                        break;
                    default:
                        setError("Smth went wrong. Try again")
                }
            });
    })


    function onSubmit(values) {
        editPassword(values)
    }

    return (

        <MainLayout>
            {LoadingEditPassword &&
                <div className='loader_wrapper'>
                    <Loader />
                </div>
            }
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
            >
                {({ errors, touched }) => (

                    <Form
                        className={cl.form_wrapper}
                    >

                        <div className={cl.top_wrapper}>
                            <GoBack />

                            <span onClick={() => console.log("errors", errors, "\n", "Touched", touched)}>
                                Edit name
                            </span>

                            <button
                                className={cl.btn}
                                type='submit'
                            >
                                Hello
                            </button>
                        </div>

                        <FormInput
                            name="old_password"
                            placeholder="old_password"
                            type="password"
                            spellCheck="false"
                            tabindex="1"
                            autoFocus={true}

                        // autoComplete="off"
                        // autoCorrect="off"
                        // autoCapitalize="none"
                        // aria-haspopup="false"
                        />
                        <FormInput
                            name="new_password"
                            placeholder="new_password"
                            type="password"
                            spellCheck="false"
                            tabindex="2"
                        />

                        <FormInput
                            name="re_new_password"
                            placeholder="re_new_password"
                            type="password"
                            spellCheck="false"
                            tabindex="2"
                        />

                        {error &&
                            <div className={cl.errorMessageWrapper}>
                                <span className={cl.errorMessage}>
                                    {error}
                                </span>
                            </div>
                        }

                    </Form>
                )}
            </Formik>
        </MainLayout>
    )
}

export default EditPasswordPage;