import axios from 'axios';
import React from 'react';
import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { getHeaders } from '../../../API/APISettings';
import Loader from '../../../components/UI/Loader/Loader';
import { useFetching } from '../../../hooks/useFetching';
import { SERVER_URL } from '../../../__CONSTANTS';

import cl from '../admin.module.scss'

function VisitorsIPsPage() {

    const [error, setError] = useState(null)


    const [visitorsIps, setVisitorsIps] = useState(null)


    const [func, loading] = useFetching(async () => {

        await axios.get(`${SERVER_URL}/admin_api/get_visitors_ips/`, {
            headers: getHeaders(),
            params: {
                _limit: 2,
                _page: 3,
                search_query: "192"
            },
        })
            .then(res => {
                setError(null)

                console.log("res", res)

                console.log(res.data.visitorsIps)

                setVisitorsIps(res.data.visitorsIps)
            })
            .catch(err => {
                switch (err.response?.status) {
                    case 403:
                        setError("403. Forbidden. Only superusers can get this page")
                        break;
                    case 401:
                        setError("Unauthorized")
                        break;
                    case 500:
                        setError("Internal Server Error")
                        break;
                    default:
                        setError("Smth went wrong. Try again")
                }
            });
    })

    useEffect(() => {
        func()
    }, [])

    return (
        <div>
            <span className={cl.reload} onClick={func}>
                reload
            </span>

            {error &&
                <h2>
                    {error}
                </h2>
            }

            {loading ?
                <Loader />
                : !error ?
                    <>
                        {visitorsIps ?
                            <div className={cl.admin_table_wrapper}>
                                <table >
                                    <thead>
                                        <tr>
                                            <th>
                                                <span className={cl.checkbox_wrapper}>
                                                    <input type="checkbox" />
                                                </span>
                                            </th>
                                            <th>
                                                id
                                            </th>
                                            <th>
                                                ip
                                            </th>
                                            <th>
                                                date
                                            </th>
                                        </tr>
                                    </thead>

                                    <tbody>
                                        {visitorsIps.map((v) =>
                                            <tr key={v.id}>
                                                <td>
                                                    <span className={cl.checkbox_wrapper}>
                                                        <input type="checkbox" />
                                                    </span>
                                                </td>
                                                <td>
                                                    {v.id}
                                                </td>
                                                <td>
                                                    <Link to={v.ip}>
                                                        {v.ip}
                                                    </Link>
                                                </td>
                                                <td>
                                                    {v.datetime}
                                                </td>
                                            </tr>
                                        )}
                                    </tbody>
                                </table>
                            </div>

                            : ''
                        }
                    </>
                    : <></>



            }

        </div >
    )
}

export default VisitorsIPsPage;


