import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import {
    NavLink,
    Link,
    useLocation,
    useNavigate
} from 'react-router-dom';
import cl from "./SidebarMenu.module.scss";
import { useSelector } from 'react-redux';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import SettingsSvg from './icons/SettingsSvg';


const SideMenuContent = () => {


    const { t } = useTranslation()

    const isUserAuthenticated = useSelector(state => state.auth.isAuthenticated)

    const user = useSelector(state => state.auth.user)
    const userIsAdmin = user?.is_staff



    return (
        <div className={cl.sidemenu}>
            <div className={`${cl.header} ${isUserAuthenticated ? cl.header_auth : cl.header_unauth}`}>
                {!isUserAuthenticated ?
                    <>
                        <SideMenuLink to="users/login">
                            login
                        </SideMenuLink>
                        <SideMenuLink to="users/login">
                            register
                        </SideMenuLink>
                    </>
                    :
                    <>
                        auth content
                    </>
                }
            </div>
            <div className={cl.main}>
                <ul>

                    <li>
                        <SideMenuLink to="/">
                            main
                        </SideMenuLink>
                    </li>

                    <li>
                        <SideMenuLink to="about-me">
                            about me
                        </SideMenuLink>
                    </li>

                    <li>
                        <SideMenuLink to="contact-me">
                            contact me
                        </SideMenuLink>
                    </li>

                </ul>
            </div>
        </div>
    )
}



const SideMenuLink = (props) => {
    const { to, children } = props

    const navigate = useNavigate()

    const onClick = (e) => {
        e.preventDefault()

        navigate(to, { replace: true })
    }

    return (
        <a
            href={to}
            onClick={onClick}
            {...props}
        >
            {children}
        </a>
    )
}



function SwipeableTemporaryDrawer(props) {
    const { sidemenuIsOpened, setSidemenuIsOpened } = props

    const location = useLocation();

    const navigate = useNavigate()


    useEffect(() => {

        let url_sidemenuopened_state = location.state?.sidebarisopened

        if (!url_sidemenuopened_state) {
            if (sidemenuIsOpened) {
                setSidemenuIsOpened(false)
            }
        }

    }, [location]);


    useEffect(() => {
        if (sidemenuIsOpened) {
            let sidebarisopened = true
            navigate(location.pathname, { state: { sidebarisopened } })
        }
    }, [sidemenuIsOpened])


    const openSidebar = () => {
        setSidemenuIsOpened(true)
    }

    const closeSidebar = () => {
        navigate(-1, { replace: true })
    }


    return (
        <SwipeableDrawer
            anchor={"left"}
            open={sidemenuIsOpened}
            disableBackdropTransition={false}
            disableDiscovery={false}
            onOpen={openSidebar}
            onClose={closeSidebar}

            sx={{
                '& .css-4t3x6l-MuiPaper-root-MuiDrawer-paper': {
                    bgcolor: 'transparent'
                }
            }}
        >
            <SideMenuContent />
        </SwipeableDrawer>
    )
}

export default SwipeableTemporaryDrawer





