import React from 'react';
import { NavLink, Link, useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';

import cl from '../Navbar.module.scss'

const DesktopMenu = function (props) {
    const {
        active, setActive
    } = props

    const isUserAuthenticated = useSelector(state => state.auth.isAuthenticated)

    const user = useSelector(state => state.auth.user)

    const location = useLocation()
    React.useEffect(() => {
        setActive(false)
    }, [location])

    return (
        <div className="container">

            <div className={`${cl.desktopMenu} ${active && cl.desktopMenu_active}`}>
                <div className={cl.desktopMenu__blackScreen} onClick={() => setActive(false)}>
                </div>

                {!isUserAuthenticated ?
                    <div className={`${cl.desktopMenu__content} ${cl.desktopMenu__content_unauth}`}>
                        <Link to="/users/login" className={cl.desktopMenu__login}>
                            login
                        </Link>
                        <br />
                        <Link to="/users/register" className={cl.desktopMenu__register}>
                            register
                        </Link>

                    </div>
                    :
                    <div className={`${cl.desktopMenu__content} ${cl.desktopMenu__content_auth}`}>
                        Yes
                    </div>
                }
            </div>
        </div>
    )
}

export default DesktopMenu;