import React from 'react';
import { Link } from 'react-router-dom';
import GoBack from '../components/GoBack/GoBack';
import KebabMenu from '../components/KebabMenu/KebabMenu';
import m from '../styles.module.scss';
import cl from './SettingsMainPage.module.scss';

import { useNavigate } from 'react-router-dom';

import axios from 'axios';

import { useFetching } from '../../../hooks/useFetching';

import { getHeaders } from '../../../API/APISettings';

import { store } from '../../../reducers/index';
import { LOGOUT_SUCCESS } from '../../../reducers/auth';
import Loader from '../../../components/UI/Loader/Loader';
import MainLayout from '../components/MainLayout/MainLayout';
import { useSelector } from 'react-redux';
import { SERVER_URL } from '../../../__CONSTANTS';

function SettingsMainPage() {
    const navigate = useNavigate()

    const username = useSelector(state => state.auth.user.username)
    const bio = useSelector(state => state.auth.user.about)

    const [logoutCurrent, LoadingLogoutCurrent] = useFetching(async () => {

        await axios.post(`${SERVER_URL}/users/logout_current/`, {}, {
            headers: getHeaders(),
        })
            .then(() => {

                new Promise(function (resolve, reject) {
                    store.dispatch({
                        type: LOGOUT_SUCCESS
                    })

                    return resolve()

                }).then(() => {
                    // alert("Success")
                    navigate("/")
                })

            })
            .catch(() => {
                alert("Error")
            });
    })


    return (
        <MainLayout>
            {LoadingLogoutCurrent &&
                <div className='loader_wrapper'>
                    <Loader />
                </div>
            }

            <div className={cl.top_wrapper}>
                <div className={cl.top_content}>
                    <GoBack />

                    <KebabMenu>
                        <span className={m.item_wrapper}>
                            <Link to="/settings/edit_name">
                                Edit name
                            </Link>
                        </span>
                        <span className={m.item_wrapper}>
                            Set photo
                        </span>
                        <span onClick={logoutCurrent} className={m.item_wrapper}>
                            Log out
                        </span>
                    </KebabMenu>
                </div>
            </div>

            <div className={cl.main}>
                <div className={cl.content}>
                    <span className={cl.title}>
                        account
                    </span>

                    <Link to="/settings/edit_username" className={cl.username}>
                        @{username}
                        <span className={cl.dlpfpfwk}>
                            username
                        </span>
                    </Link>

                    <Link to="/settings/edit_bio" className={cl.bio}>
                        <span className={cl.bio_text}>
                            {bio ? bio : "Empty"}
                        </span>
                        <span className={cl.dlpfpfwk}>
                            bio
                        </span>
                    </Link>

                </div>

                <div className={cl.content}>
                    <span className={cl.title}>
                        settings
                    </span>

                    <Link className={cl.item} to="/settings/sessions">
                        sessions
                    </Link>
                    <Link className={cl.item} to="/settings/change_password">
                        change password
                    </Link>

                </div>


                <div className={cl.content}>
                    <span className={cl.title}>
                        help
                    </span>

                    <span>

                    </span>

                </div>

            </div>

        </MainLayout>
    )
}

export default SettingsMainPage