import React from 'react'
import Loader from '../../../components/UI/Loader/Loader'
import MainLayout from '../components/MainLayout/MainLayout'

import cl from './SessionsPage.module.scss';

import { useNavigate } from 'react-router-dom';

import axios from 'axios';

import { useFetching } from '../../../hooks/useFetching';

import { getHeaders } from '../../../API/APISettings';

import { store } from '../../../reducers/index';
import { LOGOUT_SUCCESS, DELETE_ACCOUNT } from '../../../reducers/auth';
import GoBack from '../components/GoBack/GoBack';

import { useAlert } from 'react-alert';
import { SERVER_URL } from '../../../__CONSTANTS';



function SessionsPage() {


    const alert = useAlert()

    const navigate = useNavigate()

    const [logoutAllOtherSessions, LoadingLogoutAllOtherSessions] = useFetching(async () => {

        await axios.post(`${SERVER_URL}/users/logout_all_other_sessions/`, {}, {
            headers: getHeaders(),
        })
            .then((res) => {
                alert.success("success")
            })
            .catch(() => {
                alert.error("Failed")
            });
    })



    const [logoutAllSessions, LoadingLogoutAllSessions] = useFetching(async () => {

        await axios.post(`${SERVER_URL}/users/logout_all/`, {}, {
            headers: getHeaders(),
        })
            .then((res) => {
                new Promise(function (resolve, reject) {
                    store.dispatch({
                        type: LOGOUT_SUCCESS
                    })

                    return resolve()

                }).then(() => {
                    alert.success("success")

                    navigate("/")
                })

            })
            .catch(() => {
                alert.error("Error")
            });
    })


    const [deleteAccount, LoadingDeleteAccount] = useFetching(async () => {


        // let body = JSON.stringify()

        await axios.post(`${SERVER_URL}/users/delete_account/`, { "username": "James" },
            {
                headers: getHeaders(),
            })
            .then((res) => {

                alert.success(res.data.message)

                new Promise(function (resolve, reject) {
                    store.dispatch({
                        type: DELETE_ACCOUNT
                    })

                    return resolve()

                }).then(() => {
                    alert.success("account was deleted")

                    navigate("/")
                })

            })
            .catch((err) => {
                alert.error(err.response.data.message)
            });
    })


    return (
        <MainLayout>
            {LoadingLogoutAllOtherSessions | LoadingLogoutAllSessions ?
                <div className='loader_wrapper'>
                    <Loader />
                </div> : <></>
            }

            <div className={cl.top_wrapper}>
                <GoBack />
                <span>
                    Sessions
                </span>
            </div>
            <hr />
            <span onClick={logoutAllOtherSessions}>
                Logout other sessions
            </span>
            <hr />
            <span onClick={logoutAllSessions}>
                Logout all sessions
            </span>
            <hr />
            <span onClick={deleteAccount}>
                Delete account
            </span>
        </MainLayout>
    )
}

export default SessionsPage