import React, { useEffect, useState, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { useSelector } from 'react-redux';


import cl from "./Navbar.module.scss";

import SidebarMenu from '../SidebarMenu/SidebarMenu';

import useWindowSize from '../../hooks/useWindowSize';



import Logo from './_components/Logo';
import Nav from './_components/Nav';
import SelectTheme from './_components/SelectTheme';
import SelectLanguage from './_components/SelectLanguage';
import Burger from './_components/Burger';
import DesktopMenu from './_components/DesktopMenu';





function Navbar(props) {

    const { changeTheme } = props

    const isUserAuthenticated = useSelector(state => state.auth.isAuthenticated)

    const { t } = useTranslation()

    const [sidemenuIsOpened, setSidemenuIsOpened] = useState(false)

    const HEADER_MOBILE_BREAKPOINT = 543

    const [headerIsMobile, setHeaderIsMobile] = useState(false)

    const [windowWidth] = useWindowSize()

    const [desktopMenuIsActive, setDesktopMenuIsActive] = useState(false)

    const [headerHeight, setHeaderHeight] = useState(0)

    const headerRef = React.useRef()

    useEffect(() => {
        if (!headerRef.current) {
            return
        }

        const fixHeaderMargin = () => {
            let header_height = window.getComputedStyle(headerRef?.current).height

            setHeaderHeight(header_height)
        }

        fixHeaderMargin()

        window.addEventListener('resize', fixHeaderMargin)

        return () => {
            window.removeEventListener('resize', fixHeaderMargin)
        }
    }, [])

    const f_burgerClick = () => {
        if (!headerIsMobile) { // computer
            console.log("COMP")

            setDesktopMenuIsActive(true)

        } else { // phone
            console.log("PHONE")
            setSidemenuIsOpened(true)
        }
    }


    useEffect(() => {
        if (windowWidth <= HEADER_MOBILE_BREAKPOINT) {
            setHeaderIsMobile(true)
        } else {
            setHeaderIsMobile(false)
        }
    }, [windowWidth])

    return (
        <React.Fragment>
            <div style={{ height: headerHeight }}>
                {/*  */}
            </div>
            <header
                id="NAV"
                ref={headerRef}
                className={`${cl.header} ${headerIsMobile ? cl.header_mobile : cl.header_desktop}`}
            >
                <div className="container">
                    <div className={cl.header__content}>

                        <Logo />

                        <Nav
                            t={t}
                        />

                        <div className={cl.header__rightSide}>

                            <SelectLanguage />
                            <SelectTheme
                                changeTheme={changeTheme}
                            />
                            <Burger
                                onClick={f_burgerClick}
                            />

                        </div>

                    </div>
                </div>


                {!headerIsMobile &&
                    <DesktopMenu
                        active={desktopMenuIsActive}
                        setActive={setDesktopMenuIsActive}
                    />
                }

            </header>

            {headerIsMobile &&
                <SidebarMenu
                    sidemenuIsOpened={sidemenuIsOpened}
                    setSidemenuIsOpened={setSidemenuIsOpened}
                />
            }
        </React.Fragment >
    )
}

export default Navbar;