import React from 'react'
import cl from './MessageTextarea.module.scss'

interface IMessageTextareaProps {
    message: string;
    onInputChange: any;
    inputElementRef: any;
}

// : IMessageTextareaProps

const MessageTextarea = (props: any) => {

    const { message, onInputChange, inputElementRef, onKeyDown } = props

    return (
        <div className={cl.message_textarea_wrapper}>

            <textarea
                ref={inputElementRef}

                placeholder='Message'

                value={message}

                className={cl.message_input}

                onChange={onInputChange}

                rows={1}
                wrap="off"

                onKeyDown={onKeyDown}

            // {...props}
            />
        </div>
    )
}

export default MessageTextarea