import axios from "axios";
import { SERVER_URL } from "../../__CONSTANTS.js";
import { USER_LOADED, USER_LOADING, AUTH_ERROR, LOGIN_SUCCESS, LOGIN_FAIL, LOGOUT_SUCCESS, REGISTER_FAIL, REGISTER_SUCCESS } from '../auth.js';


// import { useAlert } from 'react-alert';

export const tokenConfig = getState => {
    const token = getState().auth.token;

    const config = {
        headers: {
            "Content-Type": "application/json"
        }
    }

    if (token) {
        config.headers["Authorization"] = `Token ${token}`
    }

    return config;
}


// CHECK TOKEN & LOAD USER
export const loadUser = () => (dispatch, getState) => {
    dispatch({ type: USER_LOADING });



    axios.get(`${SERVER_URL}/users/auth/get_user/`, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: USER_LOADED,
                payload: res.data,
            });
        })
        .catch((err) => {
            console.error('auth failed')
            dispatch({
                type: AUTH_ERROR,
                payload: err.response ? err.response.data.detail : "network error"
            });
        });
};


// LOGOUT USER
export const logoutUser = () => (dispatch, getState) => {
    axios.post(`${SERVER_URL}/users/auth/logout/`, null, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: LOGOUT_SUCCESS,
            });
        })
        .catch(err => {
            dispatch({
                type: AUTH_ERROR,
                payload: err.response.data
            });
        });
};


// REGISTER USER
export const registerUser = (email, user_name, first_name, password) => (dispatch, getState) => {
    const body = JSON.stringify({ email, user_name, first_name, password });

    axios.post(`${SERVER_URL}/users/auth/register`, body, tokenConfig(getState))
        .then(res => {
            dispatch({
                type: REGISTER_SUCCESS,
                payload: res.data,
            });
        })
        .catch(err => {
            console.log(err.response.data.non_field_errors)
            alert(err.response.data.non_field_errors)
            dispatch({
                type: REGISTER_FAIL,
                payload: err.response.data
            });

        });
};