import React, { useState } from 'react';
import cl from './Dropdown.module.scss'

interface IProps {
    values: string[];
    defaultValue: string;
    callback: (v: string) => any;
    headerElement?: string | any // jsx
}



function Dropdown(props: IProps) {
    const {
        values, // object
        defaultValue, // string key
        callback, // functon that receives one parameter
        headerElement, // string || jsx
    } = props


    // values={"w": "world", "h": "hello", "m":"moon"}

    if (!Object.keys(values).length) {
        throw `[-] 'values' is empty`
    }
    if (!(Object.keys(values).includes(defaultValue))) {
        console.log(values)

        throw `[-] 'values' does not contain 'value'`
    }

    const [isOpened, setIsOpened] = useState(false)

    const [value, setValue] = useState(defaultValue)

    function changeValue(v) { // str
        setValue(v)

        callback(v)
    }


    return (
        <div
            className={`${cl.dropdown} ${isOpened && cl.dropdown_active} dropdown`}
        >

            <div
                onClick={() => setIsOpened(!isOpened)}
                className={`${cl.header}`}
            >

                <div className={cl.header__content}>
                    {headerElement ?
                        headerElement
                        :
                        <span className={cl.header__title}>
                            {value}
                        </span>
                    }

                    {isOpened ?
                        <div className={`${cl.options} options`}>
                            {Object.keys(values).map(v =>
                                <span
                                    key={v}
                                    onClick={() => changeValue(v)}
                                    className={`${cl.option} ${v === value && cl.option_active}`}
                                >
                                    {values[v]}
                                </span>
                            )}
                        </div>
                        : <></>
                    }

                </div>



            </div>

            {isOpened ? <div onClick={() => setIsOpened(false)} className={cl.backScreen} /> : <></>}
        </div>
    )
}

export default Dropdown