import React, { useEffect, useState } from 'react'

function getRandomArrItem(array) {

    let randomIndex = Math.floor(Math.random() * array.length)
    return array[randomIndex]
}

interface IProps {
    text_to_type: string;
    startIn: boolean;
    secondsArray: number[];
}

export default function useTypingAnimation(props: IProps) {
    const {
        text_to_type,
        startIn,
        secondsArray,
    } = props

    const [typedText, setTypedText] = useState<string>('')

    useEffect(() => {
        if (!startIn) { return }

        const typingTimeout = setTimeout(() => {
            setTypedText(
                text_to_type.slice(0, typedText.length + 1)
            )
        }, getRandomArrItem(secondsArray))


        return () => {
            clearTimeout(typingTimeout)
        }
    }, [typedText, startIn])


    return typedText
}
