import React, { useEffect, useState } from 'react';
import { ThemeContext, themes } from '../../context/themes';
import ChatLink from '../../pages/MainPage/_components/ChatLink/ChatLink';
import Footer from '../Footer/Footer';
import Navbar from '../Navbar/Navbar';
import cl from "./BasicLayout.module.scss"

// import { CSSTransition, TransitionGroup } from 'react-transition-group'


function BasicLayout({ children }) {

    const getTheme = () => {
        let theme_from_local_storage = localStorage.getItem("theme")

        let theme_to_set = themes.includes(theme_from_local_storage) ? theme_from_local_storage : null

        if (theme_to_set) {
            return theme_to_set
        } else {
            let t = themes[0]
            localStorage.setItem("theme", t)
            return t
        }
    }

    const [theme, setTheme] = useState(getTheme())

    const changeTheme = (th) => {
        let theme_to_save = themes.includes(th) ? th : null

        if (theme_to_save) {
            setTheme(th)
            localStorage.setItem("theme", theme_to_save)
        } else {
            console.error("This theme does not exist")
        }
    }

    const d_body = document.body

    useEffect(() => {
        d_body.setAttribute("data-theme", theme.toUpperCase())

    }, [theme, d_body])

    return (

        <ThemeContext.Provider value={theme}>

            <div className={cl.mainWrapper}>

                <Navbar changeTheme={changeTheme} />

                <div className={cl.mainContentWrapper}>

                    <main className={`page ${cl.mainContent}`} >
                        {children}
                    </main>

                </div>

                <Footer />

            </div>

        </ThemeContext.Provider>
    );
}

export default BasicLayout;
