import React, { useState } from 'react'
import cl from './KebabMenu.module.scss';

function KebabMenu({ children }) {
    const [opened, setOpened] = useState(false)

    return (
        <div className={cl.main_wrapper}>
            <span onClick={() => setOpened(!opened)} className={cl.dots_wrapper}>
                <span className={cl.dots}>
                    <span className={cl.dot} />
                    <span className={cl.dot} />
                    <span className={cl.dot} />
                </span>
            </span>

            {opened &&
                <>
                    <div className={cl.content_wrapper}>
                        <div className={cl.content}>
                            {children}
                        </div>
                    </div>
                    <div onClick={() => setOpened(!opened)} className={cl.black_space} />
                </>
            }

        </div>
    )
}

export default KebabMenu;