import React, { useState, useEffect, FC } from 'react'
import * as Yup from 'yup';
import { Formik, Form } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import { SERVER_URL } from '../../../__CONSTANTS';
import Button from '../_components/FormButton/FormButton';
import Input from '../_components/FormInput/FormInput';
import { useNavigate } from 'react-router-dom';

import cl from '../authPages.module.scss';
import Loader from '../../../components/UI/Loader/Loader';
import { useTranslation } from 'react-i18next';

import { useAlert } from 'react-alert';

enum StatesOfForm {
    sendActivationPassword = 'sendActivationPassword',
    setNewPassword = 'setNewPassword',
    checkResetPassword = 'checkResetPassword',
}

function ResetPasswordPage() {
    const alert = useAlert()
    const { t } = useTranslation()
    const navigate = useNavigate()
    // 


    const [responseError, setResponseError] = useState<string>("")

    const [stateOfForm, setStateOfForm] = useState<StatesOfForm>(StatesOfForm.sendActivationPassword)


    // TODO Cancel button


    const getInitialValues = () => {
        const initial_values = {
            email: '',
            reset_password: '',
            new_password: '',
            confirm_new_password: ''
        }


        let localStorageValues = JSON.parse(
            localStorage.getItem("reset_password__form_values")!
        ) || {}


        let initial_values_keys = Object.keys(initial_values)


        let local_storage_keys_has_every_key_of_initial_values = (initial_values_keys.every(i => Object.keys(localStorageValues).includes(i)))

        if (!local_storage_keys_has_every_key_of_initial_values) {
            return initial_values
        }

        let values_to_return = {};

        initial_values_keys.forEach(i => {
            values_to_return[i] = localStorageValues[i]
        })

        return values_to_return;
    }


    const initialValues = getInitialValues()
    // const initialValues = {
    //     email: '',
    //     reset_password: '',
    //     new_password: '',
    //     confirm_new_password: ''
    // }

    useEffect(() => {

        if (initialValues["email"]) {

            if (initialValues["reset_password"]) {
                setStateOfForm(StatesOfForm.setNewPassword)
            } else {
                setStateOfForm(StatesOfForm.checkResetPassword)
            }

        }

    }, []) // initialValues


    const reactToChangingOfFormState = () => {
        switch (stateOfForm) {

            case StatesOfForm.sendActivationPassword:
                break;
            case StatesOfForm.checkResetPassword:
                // setDisabledInputs(["email"])
                // setButtonText("Check the reset password")
                setValidationSchemas({
                    ...validationSchemas,
                    reset_password: Yup.string()
                        .required(required)
                        .length(5)
                    // .test('len', 'Must be exactly 5 characters', val => val ? String(val).length === 5 : false)
                })
                break;
            case StatesOfForm.setNewPassword:
                // setDisabledInputs(["email"])
                setValidationSchemas({
                    ...validationSchemas,
                    new_password: Yup.string()
                        .required(required),
                    confirm_new_password: Yup.string()
                        .required(required)

                })
                break;
            default:
                console.error("something went wrong")
                break;
        }
    }





    useEffect(() => {

        reactToChangingOfFormState()

    }, [stateOfForm])



    let required = t('main_content.pages.auth_pages.required', "Required")


    const [validationSchemas, setValidationSchemas] = useState<object>({
        email: Yup.string()
            .email(t('main_content.pages.auth_pages.ief', "Invalid email format"))
            .required(required),
    })


    const YupValidation = Yup.object().shape({
        ...validationSchemas
    })

    const [sendInstructions, LoadingSendInstructions] = useFetching(async (values: object) => {
        const body = JSON.stringify(values);

        await axios.post(`${SERVER_URL}/users/reset_password__send_instructions/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)

                setStateOfForm(StatesOfForm.checkResetPassword)

                setResponseError("")

                localStorage.setItem("reset_password__form_values", body)

            })
            .catch(err => {

                setResponseError(
                    err.response?.data?.message
                    ||
                    'Smth went wrong. Try again.')
            });

    })


    const [checkResetPassword, LoadingCheckResetPassword] = useFetching(async (values: object) => {
        const body = JSON.stringify(values);

        await axios.post(`${SERVER_URL}/users/reset_password__check_reset_password/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)

                setStateOfForm(StatesOfForm.setNewPassword)

                setResponseError("")


                localStorage.setItem("reset_password__form_values", body)

            })
            .catch(err => {

                setResponseError(
                    err.response?.data?.message
                    ||
                    'Smth went wrong. Try again.')
            });
    })


    const [changePassword, LoadingChangePassword] = useFetching(async (values: object) => {
        const body = JSON.stringify(values);

        await axios.post(`${SERVER_URL}/users/reset_password__set_new_password/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)


                setResponseError("")

                localStorage.removeItem("reset_password__form_values")

                navigate("/users/login")

            })
            .catch(err => {

                setResponseError(
                    err.response?.data?.message
                    ||
                    'Smth went wrong. Try again.')
            });
    })


    // TODO
    // LoadibgCancelRegistration
    function cancelResetingPassword() {
        console.log("TODO")
    }


    function onSubmit(values: object) {

        switch (stateOfForm) {
            case StatesOfForm.sendActivationPassword:
                sendInstructions(values)
                break;
            case StatesOfForm.checkResetPassword:
                checkResetPassword(values)
                break;
            case StatesOfForm.setNewPassword:
                changePassword(values)
                break;
            default:
                console.error("something went wrong")
                break;
        }

    }



    return (
        <>
            {(LoadingSendInstructions || LoadingCheckResetPassword || LoadingChangePassword) &&
                <div className='loader_wrapper'>
                    <Loader />
                </div>
            }
            {responseError &&
                <div className={`${cl.errorMessageWrapper}`}>
                    <span className={`${cl.errorMessage} errorMessage`}>
                        {responseError}
                    </span>
                </div>
            }

            <div className={cl.form_wrapper}>
                <Formik
                    initialValues={initialValues}
                    validationSchema={YupValidation}
                    onSubmit={onSubmit}
                >
                    {({ errors, touched, values }) => (
                        <Form
                            className={`${cl.form} form`}
                            autoComplete='off'
                        >
                            <b className={`${cl.page_name} auth_page_name`}>
                                Reseting password
                            </b>

                            <Input
                                name="email"
                                placeholder={t('main_content.pages.auth_pages.email', "email")}
                                type="email"
                                autoFocus={true}
                                value={values["email"]}
                                touched={touched["email"]}
                                hasError={errors["email"]}
                                spellCheck="false"

                                autoComplete="true"

                                disabled={
                                    stateOfForm === StatesOfForm.checkResetPassword
                                    ||
                                    stateOfForm === StatesOfForm.setNewPassword
                                }
                            />

                            {(() => {
                                switch (stateOfForm) {
                                    case StatesOfForm.sendActivationPassword:
                                        return (
                                            <>
                                            </>
                                        )
                                    case StatesOfForm.checkResetPassword:
                                        return (
                                            <Input
                                                name="reset_password"
                                                placeholder="reset password"
                                                type="text"

                                                inputMode="numeric"


                                                value={values["reset_password"]}
                                                touched={touched["reset_password"]}
                                                hasError={errors["reset_password"]}
                                            />
                                        )
                                    case StatesOfForm.setNewPassword:
                                        return (
                                            <>
                                                <Input
                                                    name="new_password"
                                                    placeholder="new password"
                                                    type="password"

                                                    value={values["new_password"]}
                                                    touched={touched["new_password"]}
                                                    hasError={errors["new_password"]}

                                                    autoComplete="off"
                                                />

                                                <Input
                                                    name="confirm_new_password"
                                                    placeholder="confirm_new_password"
                                                    type="password"

                                                    value={values["confirm_new_password"]}
                                                    touched={touched["confirm_new_password"]}
                                                    hasError={errors["confirm_new_password"]}


                                                    autoComplete="off"
                                                />
                                            </>
                                        )
                                    default:
                                        return (
                                            <>
                                                ERROR
                                            </>
                                        )
                                }
                            })()}

                            <Button
                                // disabled={
                                //     LoadingLoginUser ||
                                // !(isEmpty(errors) && !isEmpty(touched))
                                // }
                                type="submit"
                            >
                                {(() => {
                                    switch (stateOfForm) {
                                        case StatesOfForm.sendActivationPassword:
                                            return 'Send instructions'
                                        case StatesOfForm.checkResetPassword:
                                            return 'Check the reset password'
                                        case StatesOfForm.setNewPassword:
                                            return 'Set new password'
                                        default:
                                            return "ERROR"
                                    }
                                })()}
                            </Button>

                            {
                                (
                                    stateOfForm === StatesOfForm.checkResetPassword ||
                                    stateOfForm === StatesOfForm.setNewPassword
                                ) ?
                                    <div className={cl.cancelBtnWrapper}>
                                        <span className={cl.cancelBtn} onClick={cancelResetingPassword}>
                                            cancel
                                        </span>
                                    </div>
                                    : <></>
                            }

                        </Form>
                    )}
                </Formik>
            </div>
        </>
    )
}

export default ResetPasswordPage;